<template>
    <div class="container wrapper" :class="[
                state.fadeClass,
                state.visibilityClass,
                state.mouseClass
            ]">
        <div class="split">
            <div id="ChoiceButtons">
                <!-- Empty divs are used as containers for the conversation triangles. -->
                <div :class="{ highlight: state.progress == 0 }"></div>
                <div :class="{ highlight: state.progress == 2 }"></div>
                <div :class="{ highlight: state.progress == 1 }"></div>
                <div :class="{ highlight: state.progress == 3 }"></div>
                <div v-if="state.progress < 4" id="decks">
                    <div v-if="state.progress <= 1" class="choice global-hover" @click="chooseCardType('Haaste')">
                        <img src="../assets/cardStack.png" alt="ValintaPakka">
                        <p>Haaste</p>
                    </div>
                    <div v-if="state.progress <= 1" class="choice global-hover" @click="chooseCardType('Toive')">
                        <img src="../assets/cardStack.png" alt="ValintaPakka">
                        <p>Toive</p>
                    </div>
                    <div v-if="state.progress <= 1" class="choice global-hover" @click="chooseCardType('Ongelma')">
                        <img src="../assets/cardStack.png" alt="ValintaPakka">
                        <p>Ongelma</p>
                    </div>
                     <div v-if="state.progress > 1" class="choice global-hover" @click="chooseCardType('Voimavara')">
                        <img src="../assets/cardStack.png" alt="ValintaPakka">
                        <p>Voimavara</p>
                    </div>
                </div>
            </div>
        </div>
        <div class="split">
            <div class="trigenda-wrapper">
                <img src="../assets/Alusta_tumma.png">
                <div id="trigendaBoard">
                    <div>
                        <div v-if="state.progress >= 0" :class="{ triangle2: state.progress > 0 }" class="triangle"></div>
                    </div>
                    <div>
                        <div v-if="state.progress >= 2" :class="{  triangle2: state.progress > 2 }" class="triangle"></div>
                    </div>
                    <div>
                        <div v-if="state.progress >= 1" :class="{  triangle2: state.progress > 1 }" class="triangle"></div>
                    </div>
                    <div>
                        <div v-if="state.progress >= 3" :class="{  triangle2: state.progress > 3 }" class="triangle"></div>
                    </div>
                </div>
            </div>
            <div id="tips">
            <p><span class="bold">Tavoite: </span>Tavoite on ongelma, haaste tai toive, jonka pyrimme keskustelussa ratkaisemaan.</p>
            <p><span class="bold">Ongelma: </span>Ongelma hidastaa sinua tavoitteesi saavuttamisessa.</p>
            <p><span class="bold">Voimavara: </span>Koet tmn asian vahvuutenasi, saat siit voimaa ja se auttaa jaksamaan.</p>
            </div>
            <!-- Show "Next stage" button if state.overlayIsActive is set to false -->
            <Tip v-if="!state.backToStart && props.showInfo" :tipMsg="state.tipMsg[state.progress]" :mood="state.tipMood" />
        </div>

        <!-- Overlay is a component where card selection and answer choices are
        made for a current card placeholder shown in Phase2 component. Overlay is shown over the
        Phase2 component and Phase2 is shown transparent behind the Overlay.
        -->
        <Overlay v-if="state.overlayVisibility"
            :cardType="state.overlayCardType"
            :selectionType="state.selectionType"
            @deactivateOverlay="deactivateOverlay"
            @phasecomplete="finishPhase"
            @goBack="goBack" />
    </div>
</template>

<script>

import Card from "./Card.vue";
import Answer from "./Answer.vue";
import Overlay from "./Overlay.vue";
import Tip from './Tip.vue';
import { reactive } from "vue";
import Summary from './Summary.vue';

export default {
    name: "Phase2",
    props: {
        msg: String,
        showInfo: Boolean
    },
    components: {
        Card,
        Answer,
        Overlay,
        Tip,
        Summary
    },
    setup(props, { emit } ) {

        const state = reactive({
            // Fade effect CSS class for Container div. ('fadeIn' or 'fadeOut')
            fadeClass: null,
            // Visibility CSS class for Container div. ('visible' or 'hidden')
            visibilityClass: 'hidden',

            /**
             * Mouse CSS class for Container div,
             * tells if mouse clicks are enabled or disabled. ('mouseEnabled' or 'mouseDisabled')
             */
            mouseClass: 'mouseDisabled',
            // Overlay is shown or hidden based on state.overlayVisibility boolean value.
            overlayVisibility: false,
            // OverlaycardType tells what kind of cards are shown in Overlay's CardSelection component.
            overlayCardType: null,
            // SelectionType tells where the card will be placed in Trigenda triangle.
            selectionType: null,

            /**
             * SelectionType, Tringo's message are set based on progress index. The "Next Phase"
             * button is shown if progress is 4.
             */
            progress: 0,
            cardArray: JSON.parse(localStorage.getItem("cardArray")),
            // Tringo's messages. Strings separated by comma in the arrays creates a line break in the message.
            tipMsg: [
                ['Olet nyt suorittanut keskustelun ensimmisen vaiheen. Seuraavaksi valitset edellisen vaiheen ongelmista, haasteista tai toiveista kortin keskustelun tavoitteeksi.', 'Tavoite on asia, johon pyrimme keskustelun avulla lytmn ratkaisuja. Tst eteenpin pyydn sinua mys avaamaan valintojasi kirjoittamalla tekstikenttiin.'],
                ['Hienoa! Nyt keskustelulla on tavoite. Seuraavaksi valitsemme haasteen, joka hidastaa tmn tavoitteen toteutumista.', 'Sinulla on mahdollisuus valita joko ongelma, haaste tai toive. Tarvittaessa voit tutkia mys edellisen vaiheen "En osaa sanoa" -kortteja.'],
                ['Nyt valittuna ovat tavoite sek haaste. Seuraavaksi alamme ratkomaan haastetta voimavarojasi kytten.', 'Valitse seuraavaksi ensimminen merkittv voimavarasi. Tarvittaessa voit tutkia mys edellisen vaiheen "En osaa sanoa" -kortteja.'],
                ['Hyv! Valitsethan viel toisen voimavaran.'],
            ],
            // Sets the mood of Tringo, passed to Tip component. 'happy', 'confused' or 'sad'
            tipMood: 'happy',
        });

        const fadeInElements = () => {
            state.fadeClass = 'fadeIn';
            state.visibilityClass = 'visible';
            setTimeout(() => {
                state.mouseClass = 'mouseEnabled';
            }, 200);
        }

        fadeInElements();

        const activateOverlay = () => {
            state.overlayVisibility = true;
        }

        const deactivateOverlay = () => {
            state.overlayVisibility = false;
            state.progress++;

            if (state.progress === 4)
                finishPhase();
        }

        const finishPhase = () => {
            emit("phasecomplete");
        }
        const chooseCardType = (cardType) => {
            state.overlayCardType = cardType;

            if (state.progress === 0)
                state.selectionType = "Tavoite"
            else if (state.progress === 1)
                state.selectionType = "Haaste"
            else if (state.progress === 2)
                state.selectionType = "Voimavara1"
            else if (state.progress === 3)
                state.selectionType = "Voimavara2"
            activateOverlay();
        }
        const goBack = () => {
            state.overlayCardType = null;
            state.selectionType = null;
            state.overlayVisibility = false;
        }
        return { activateOverlay, state, deactivateOverlay, finishPhase, chooseCardType, goBack, props };
    },
    emits: ['phasecomplete'],
};

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .wrapper {
        display: flex;
        height: 90vh;
    }

    .split {
        width: 50%;
        height: 91.2vh;
        justify-content: center;
        margin: auto 0;
    }
    .split:nth-child(2) {
        background-image: url('../assets/tausta_blue.jpg');
        background-size: cover;
        background-repeat: no-repeat;
    }
    .split:first-child {
        background-color: var(--left);
    }

    .split:nth-child(2) img {
        position: absolute;
        transform: translate(-350px, -75px) scale(0.65);
        z-index: 40;
    }

    .split:nth-child(2) .content {
        margin-top: 170px;
    }
    .choice{
        width: 80%;
        height: 80%;
    }
    .choice > img{
        width: 80%;
    }

    p {
        margin:0;
    }
    #tips{
        text-align:left;
        width: 90%;
        margin-left: auto;
        margin-right: auto;
        margin-top: 5em;
    }

    #decks{
        display: inline-flex;
    }

    #ChoiceButtons{
        margin-top: 15em;
    }

    .trigenda-wrapper {
        margin-top: 16%;
    }

    #trigendaBoard{
        width: 459px;
        height: 400px;
        margin: 30px auto;
        display: flex;
        flex-wrap: wrap;
    }
    #trigendaBoard div {
        padding: 10px;
        width: 90px;
        height: 100px;
        margin: auto;
        transform: scale(1.17);
    }

    #trigendaBoard div:nth-child(1) {
        width: 100%;
        margin: auto;
        padding: 0 30px;
    }

    #trigendaBoard div:nth-child(1) div {
        width: 90px;
        position: relative;
        top: -5px;
        z-index: 10px;
        transform: translateY(24px) translateX(1px) scale(1.2);
    }

    #trigendaBoard div:nth-child(2) div {
        position: relative;
        top: 5px;
        transform: translateY(22px) translateX(-2px) scale(1.2);
    }

    #trigendaBoard div:nth-child(3) div {
        transform: rotate(180deg) scale(1.2) translateY(-20px);
        position: relative;
        right: 29px;
    }

    #trigendaBoard div:nth-child(4) div {
        position: relative;
        right: 58px;
        top: 5px;
        transform: translateY(22px) translateX(-2px) scale(1.2);
    }

    .triangle {
        width: 40px;
        height: 80px;
        clip-path: polygon(50% 0%, 10% 100%, 90% 100%);
        transition: 250ms background-color ease;
        background-color: var(--btn_hover);
    }
    .triangle2{
        background-color: white;
    }

    @media only screen and (max-width: 600px) {
        .wrapper {
            flex-direction: column;
        }

        .split {
            width: 100%;
        }

        .temporary {
            display: none;
        }
    }
</style>
